.featured {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.featuredItem {
  flex: 1;
  margin: 0px 20px;
  padding: 30px;
  border-radius: 10px;
  cursor: pointer;
  box-shadow: 10px 10px 5px -1px rgba(0, 0, 0, 0.31);
  -webkit-box-shadow: 10px 10px 5px -1px rgba(0, 0, 0, 0.31);
  -moz-box-shadow: 10px 10px 5px -1px rgba(0, 0, 0, 0.31);
  margin-top: 30px;
}

.featuredTitle {
  font-size: 20px;
}

.featuredMoneyContainer {
  margin: 10px 0px;
  display: flex;
  align-items: center;
}

.featuredMoney {
  font-size: 30px;
  font-weight: 600;
}

.featuredMoneyRate {
  display: flex;
  align-items: center;
  margin-left: 20px;
}
